import { useEffect, useState } from 'react';
import { Sidebar as ProSidebar, Menu, MenuItem, SubMenu } from 'react-pro-sidebar';
import React from 'react';
import { effect } from '@preact/signals-react';
import { usePathname } from 'next/navigation';
import { useSignals } from '@preact/signals-react/runtime';
// import { ThemeMode, RouterChange } from '@/utils/app.event';
import { RouterChange } from '@/utils/app.event'; // ThemeMode removed for now or check if it exists
// import { AppRouter } from '@/utils/app.router';
// import TypeButton from '@/types/type.button';
import TypeIcon from '@/types/type.icon';
import {
  menuAccessDataSignal,
  menuAccessIsLoadingSignal,
  fetchMenuAccess,
} from '@/modules/menu/common/service';
import { ThemeMode } from '@/utils/app.event';
import { SkeletonMenu } from '@/skeleton/skeleton-menu';

type Theme = 'light' | 'dark';

export const Sidebar = ({
  isOpen = false,
  onToggle,
}: {
  isOpen: boolean;
  onToggle: () => void;
}) => {
  useSignals();
  const [theme, setTheme] = React.useState<Theme>('light');
  const [toggled, setToggled] = useState(isOpen);
  const pathname = usePathname();
  const [isMounted, setIsMounted] = useState(false);

  useEffect(() => {
    setIsMounted(true);
  }, []);

  useEffect(() => {
    setToggled(isOpen);
  }, [isOpen]);

  const handleToggle = () => {
    setToggled(!toggled);
    onToggle();
  };

  useEffect(() => {
    // Theme logic temporarily simplified or removed if ThemeMode doesn't exist yet
    effect(() => {
      const currentTheme =
        ThemeMode.value === 'dark' || ThemeMode.value === 'night' ? 'dark' : 'light';
      setTheme(currentTheme);
    });

    // Fetch menu access if signal is undefined or empty
    const loadMenuAccess = async () => {
      const currentValue = menuAccessDataSignal.value;
      const isEmpty =
        currentValue === undefined || (Array.isArray(currentValue) && currentValue.length === 0);

      if (isEmpty && !menuAccessIsLoadingSignal.value) {
        await fetchMenuAccess();
      }
    };

    loadMenuAccess();
  }, []);

  function handleMenu(href: string | undefined): void {
    // handleToggle(); // Auto close on mobile?
    if (!href) return;
    RouterChange(href, {});
  }

  // Define menu item styles based on the current theme
  const menuItemStyles = {
    button: ({ active, disabled }: { level: number; active: boolean; disabled: boolean }) => {
      const baseBackgroundColor = themes[theme].menu.menuContent;

      return {
        color: disabled ? themes[theme].menu.disabled.color : themes[theme].menu.color,
        backgroundColor: active ? themes[theme].menu.hover.backgroundColor : baseBackgroundColor,
        '&:hover': {
          backgroundColor: themes[theme].menu.hover.backgroundColor,
          color: themes[theme].menu.hover.color,
        },
      };
    },
    subMenuContent: {
      backgroundColor: 'transparent',
    },
    icon: ({ disabled }: { active: boolean; disabled: boolean }) => ({
      color: disabled ? themes[theme].menu.disabled.color : themes[theme].menu.icon,
    }),
  };

  if (!isMounted) {
    return null;
  }

  return (
    <div className='h-[calc(100vh-82px)]'>
      <div className='card bg-white dark:bg-black shadow-xl h-full overflow-hidden border border-base-300'>
        <ProSidebar
          backgroundColor='transparent'
          breakPoint='xl'
          className='h-full'
          rootStyles={{
            color: themes[theme].sidebar.color,
            backgroundColor: 'transparent',
            border: 'none',
          }}
          toggled={isOpen}
          onBackdropClick={handleToggle}
        >
          <SidebarHeader />
          <Menu menuItemStyles={menuItemStyles}>
            {menuAccessIsLoadingSignal.value ? (
              <SkeletonMenu />
            ) : (
              (menuAccessDataSignal.value || []).map((item, index) => {
                const canReadParent = item.permissions?.read ?? true;

                if (!canReadParent) {
                  return null;
                }

                const isParentActive = item.children
                  ? item.children.some(
                    (child) => pathname === child.href && (child.permissions?.read ?? true),
                  ) || pathname === item.href
                  : pathname === item.href;

                return item.children ? (
                  <section key={`${item.name}-${index}`}>
                    <SubMenu
                      key={`${item.name}-${index}`}
                      active={isParentActive}
                      defaultOpen={isParentActive}
                      icon={
                        item.icon ? (
                          <TypeIcon color={themes[theme].menu.icon} name={item.icon} />
                        ) : undefined
                      }
                      label={item.name}
                    >
                      {item.children.map((child, childIndex) => {
                        const canReadChild = child.permissions?.read ?? true;

                        if (!canReadChild) {
                          return null;
                        }

                        return (
                          <section key={`${child.name}-${childIndex}`}>
                            <MenuItem
                              key={`${child.name}-${childIndex}`}
                              active={pathname === child.href}
                              icon={
                                child.icon ? (
                                  <TypeIcon color={themes[theme].menu.icon} name={child.icon} />
                                ) : undefined
                              }
                              onClick={() => handleMenu(child.href)}
                            >
                              {child.name}
                            </MenuItem>
                          </section>
                        );
                      })}
                    </SubMenu>
                  </section>
                ) : (
                  <section key={`${item.name}-${index}`}>
                    <MenuItem
                      key={`${item.name}-${index}`}
                      active={pathname === item.href}
                      icon={
                        item.icon ? (
                          <TypeIcon color={themes[theme].menu.icon} name={item.icon} />
                        ) : undefined
                      }
                      onClick={() => handleMenu(item.href)}
                    >
                      {item.name}
                    </MenuItem>
                  </section>
                );
              })
            )}
          </Menu>
        </ProSidebar>
      </div>
    </div>
  );
};

function SidebarHeader() {
  return (
    <aside className='sticky top-0 z-10 bg-white dark:bg-black border-b border-base-300'>
      <div className='sticky top-0 flex items-center justify-between p-4'>
        <h2 className='text-md font-medium text-[#607489]'>Welcome</h2>
      </div>
    </aside>
  );
}

const themes = {
  light: {
    sidebar: {
      backgroundColor: '#ffffff',
      color: '#607489',
    },
    menu: {
      fontSize: 'small',
      menuContent: '#ffffff',
      color: '#607489',
      icon: '#0098e5',
      hover: {
        backgroundColor: '#c5e4ff',
        color: '#44596e',
      },
      disabled: {
        color: '#9fb6cf',
      },
    },
  },
  dark: {
    sidebar: {
      backgroundColor: '#000000',
      color: '#8ba1b7',
    },
    menu: {
      fontSize: 'small',
      menuContent: '#000000',
      color: '#8ba1b7',
      icon: '#59d0ff',
      hover: {
        backgroundColor: '#00458b',
        color: '#b6c8d9',
      },
      disabled: {
        color: '#3e5e7e',
      },
    },
  },
};
